.wrapper{
   width: 100%;
}
.page {
   display: flex;
   justify-content: center;
   opacity: 0.6; 
}
.container{
   width: 95%;
   display: flex;
   flex-direction: column;
   align-items: center;
}
.body {
   display: flex;
   justify-content: space-between;
   width: 100%;
}
.content {
   width: 100%;
   padding: 3.5% 0;
}

.title {
   color: #FFF;
   font-family: 'Roboto Slab', serif;
   @include adaptiveFont(35, 30);
   @extend %fontStyleMedium;
}
.center {
   text-align: center;
   margin-bottom: 30px;
}